if (typeof require !== "undefined") {
	var React = require("react");
	var _ = require("lodash");
}

class FilterablePartnerEntries extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			regionSelected: "",
			countrySelected: "",
			msspChecked: true,
			varChecked: true,
			vadChecked: true,
		};

		this.handleRegionSelectChange = this.handleRegionSelectChange.bind(this);
		this.handleCountrySelectChange = this.handleCountrySelectChange.bind(this);
		this.handleMsspChange = this.handleMsspChange.bind(this);
		this.handleVarChange = this.handleVarChange.bind(this);
		this.handleVadChange = this.handleVadChange.bind(this);
	}

	handleRegionSelectChange(regionSelected) {
		this.setState({
			regionSelected: regionSelected,
			countrySelected: ""
		});
	}

	handleCountrySelectChange(countrySelected) {
		this.setState({
			countrySelected: countrySelected,
			regionSelected: ""
		});
	}

	handleMsspChange(msspChecked) {
		this.setState({
			msspChecked: msspChecked
		});
	}

	handleVarChange(varChecked) {
		this.setState({
			varChecked: varChecked
		});
	}

	handleVadChange(vadChecked) {
		this.setState({
			vadChecked: vadChecked
		});
	}

	render() {
		return (
			<div>
				<div className="q-default-hero q-default-hero--">
					<div className="q-default-hero__container">

						<div className="q-default-hero__content heroContent">
							<h1 className="q-default-hero__heading">Find a Cyber Security partner.</h1>
							<h2 className="q-default-hero__subheading">To find a Qualys partner in your region, please select from the list below.</h2>
						</div>

						<div className="q-default-hero__splash">

							<div className="q-default-hero__box-wrap heroBox">

								<FilterBox
									partners={this.props.partners}
									regionSelected={this.state.regionSelected}
									countrySelected={this.state.countrySelected}
									msspChecked={this.state.msspChecked}
									varChecked={this.state.varChecked}
									vadChecked={this.state.vadChecked}
									onRegionSelectChange={this.handleRegionSelectChange}
									onCountrySelectChange={this.handleCountrySelectChange}
									onMsspChange={this.handleMsspChange}
									onVarChange={this.handleVarChange}
									onVadChange={this.handleVadChange}
								/>
							</div>

							<div className="q-default-hero__image heroImage">
							</div>
						</div>
					</div>
				</div>
				<div className="q-grid__container">
					<div className="q-grid__inner">
						<div className="q-grid__row">
							<div className="q-grid__column q-grid__column-10 partners">

								<div className="partners-local">
									<h2 className="heading--2">Local:</h2>
									<p className="no-partners-found hidden paragraph--1">No local partners found.</p>
									<PartnerEntry
										partners={this.props.partners}
										isWorldwide="false"
										regionSelected={this.state.regionSelected}
										countrySelected={this.state.countrySelected}
										msspChecked={this.state.msspChecked}
										varChecked={this.state.varChecked}
										vadChecked={this.state.vadChecked}
									/>
								</div>
								<div className="partners-worldwide">
									<h2 className="heading--2">Worldwide:</h2>
									<p className="no-partners-found hidden">No worldwide partners found in this region / country.</p>
									<PartnerEntry
										partners={this.props.partners}
										isWorldwide="true"
										regionSelected={this.state.regionSelected}
										countrySelected={this.state.countrySelected}
										msspChecked={this.state.msspChecked}
										varChecked={this.state.varChecked}
										vadChecked={this.state.vadChecked}
									/>
								</div>
								<div className="clear"></div>
								<br />
								<p className="note">Partners may serve multiple territories. For regional offices please visit the partner website or call the # listed.</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

class RegionCountrySelectFields extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			region: "",
			country: ""
		};

		this.regionSelect = React.createRef();
		this.countrySelect = React.createRef();

		this.handleRegionSelectChange = this.handleRegionSelectChange.bind(this);
		this.handleCountrySelectChange = this.handleCountrySelectChange.bind(this);
	}

	handleRegionSelectChange(e) {
		this.props.onRegionSelectChange(e.target.value);
		this.setState({
			region: e.target.value,
			country: ""
		});
	}

	handleCountrySelectChange(e) {
		this.props.onCountrySelectChange(e.target.value);
		this.setState({
			region: "",
			country: e.target.value
		});
	}

	render() {

		var allRegions = this.props.partners.map((partner) => {
			var someRegions = partner.regions.map((region) => {
				return region;
			});
			return someRegions[0];
		});

		var regionsWithoutNull = allRegions.filter(region => region.id);

		var sortedRegions = _.sortBy(regionsWithoutNull, "id");

		var sortedUniqueRegions = _.sortedUniqBy(sortedRegions, region => region.id);

		var regionOptionNodes = sortedUniqueRegions.map(function (region) {
			return (
				<option key={region.id} value={region.id}>{region.name}</option>
			);
		});

		var countries = this.props.partners.map((partner) => {
			return {
				"country": partner.country,
				"countryId": partner.countryId
			};
		});

		var countriesWithoutNull = countries.filter(country => country.countryId);

		var sortedCountries = _.sortBy(countriesWithoutNull, "countryId");

		var sortedUniqueCountries = _.sortedUniqBy(sortedCountries, country => country.countryId);

		var countryOptionNodes = sortedUniqueCountries.map(function (country) {
			return (
				<option key={country.countryId} value={country.countryId}>{country.country}</option>
			);
		});

		return (
			<div>
				<label htmlFor="region">Region</label>
				<select
					name="region"
					ref={this.regionSelect}
					value={this.state.region}
					className="q-form__select"
					onChange={this.handleRegionSelectChange}>
					<option value="">Region</option>
					<option value="all">All Regions</option>
					{regionOptionNodes}
				</select>

				<label htmlFor="country">Country</label>
				<select
					name="country"
					ref={this.countrySelect}
					value={this.state.country}
					className="q-form__select"
					onChange={this.handleCountrySelectChange}>
					<option value="">Country</option>
					<option value="all">All Countries</option>
					{countryOptionNodes}
				</select>
			</div>
		);
	}
}

class FilterBox extends React.Component {
	constructor(props) {
		super(props);
		this.handleRegionSelectChange = this.handleRegionSelectChange.bind(this);
		this.handleCountrySelectChange = this.handleCountrySelectChange.bind(this);
		this.handleMsspChange = this.handleMsspChange.bind(this);
		this.handleVarChange = this.handleVarChange.bind(this);
		this.handleVadChange = this.handleVadChange.bind(this);
	}

	handleRegionSelectChange(region) {
		this.props.onRegionSelectChange(region);
	}

	handleCountrySelectChange(country) {
		this.props.onCountrySelectChange(country);
	}

	handleMsspChange(e) {
		this.props.onMsspChange(e.target.checked);
	}

	handleVarChange(e) {
		this.props.onVarChange(e.target.checked);
	}

	handleVadChange(e) {
		this.props.onVadChange(e.target.checked);
	}

	render() {
		return (
			<div className="q-square">
				<div className="q-square__inner">
					<div className="q-square__content">
						<RegionCountrySelectFields
							partners={this.props.partners}
							region={this.props.region}
							onRegionSelectChange={this.handleRegionSelectChange}
							country={this.props.country}
							onCountrySelectChange={this.handleCountrySelectChange}
						/>
						<label htmlFor="type">Partner types:</label>
						<div className="type">
							<div className="type-container">
								<input
									name="type"
									value="msp"
									id="msp"
									type="checkbox"
									checked={this.props.msspChecked}
									onChange={this.handleMsspChange}
								/>
								<label htmlFor="msp"><abbr title="Managed security service provider">MSP/MSSP</abbr> Partner</label>
							</div>
							<div className="type-container">
								<input
									name="type"
									value="var"
									id="var"
									type="checkbox"
									checked={this.props.varChecked}
									onChange={this.handleVarChange}
								/>
								<label htmlFor="var"><abbr title="Value-Added Reseller (VAR)">Value-Added Reseller (VAR)</abbr></label>
							</div>
							<div className="type-container">
								<input
									name="type"
									value="vad"
									id="vad"
									type="checkbox"
									checked={this.props.vadChecked}
									onChange={this.handleVadChange}
								/>
								<label htmlFor="vad"><abbr title="Value-Added Distributor (VAD)">Value-Added Distributor (VAD)</abbr></label>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

class PartnerBadges extends React.Component {
	render() {
		var varBadge, vadBadge, mspBadge;

		if (this.props.isMsp) {
			mspBadge = <div className="partner__badge partner__badge--msp">MSSP</div>;
		}

		if (this.props.isVar) {
			varBadge = <div className="partner__badge partner__badge--var">VAR</div>;
		}

		if (this.props.isVad) {
			vadBadge = <div className="partner__badge partner__badge--vad">VAD</div>;
		}

		return (
			<div className="badge-container">
				{mspBadge}
				{varBadge}
				{vadBadge}
			</div>
		);
	}
}


class PartnerEntry extends React.Component {
	render() {
		var partnerEntries = this.props.partners.map((partner) => {
			var partnerTypeCheckPassed = false;
			var regionCheckPassed = false;
			var countryCheckPassed = false;

			if ((partner.isMsp && this.props.msspChecked) ||
				(partner.isVar && this.props.varChecked) ||
				(partner.isVad && this.props.vadChecked)) {
				partnerTypeCheckPassed = true;
			}

			// if user-selected region is in semi-colon-delimited list of partner regions
			if ((partner.regionId.indexOf(this.props.regionSelected) >= 0) ||
				this.props.regionSelected === "all" ||
				this.props.regionSelected === "") {
				regionCheckPassed = true;
			}

			if ((partner.countryId === this.props.countrySelected) ||
				this.props.countrySelected === "all" ||
				this.props.countrySelected === "") {
				countryCheckPassed = true;
			}

			if (partner.isWorldwide === undefined) {
				partner.isWorldwide = "false";
			}

			if ((partner.isWorldwide.toString() === this.props.isWorldwide)
				&& partnerTypeCheckPassed
				&& regionCheckPassed
				&& countryCheckPassed) {
				var partnerName = partner.name;
				if (partner.website !== null && partner.website !== "") {
					partnerName = <a href={partner.website} target="_blank" rel="noopener noreferrer">{partner.name}</a>;
				}

				var partnerAddress = "";
				if (partner.city !== null) {
					partnerAddress = partner.city + "," + partnerAddress;
				}

				if (partner.state !== null) {
					partnerAddress = partnerAddress + " " + partner.state;
				}

				if (partner.country !== null) {
					partnerAddress = partnerAddress + " " + partner.country;
				}

				var addressAndPhone = "";
				if (partnerAddress.trim() !== "" && partner.phone !== null) {
					addressAndPhone = <p>{partnerAddress}<br/>{partner.phone}</p>;
				} else if (partner.phone !== null) {
					addressAndPhone = partner.phone;
				} else {
					addressAndPhone = partnerAddress;
				}

				return (
					<div key={partner.id} className="partner">
						<p className="paragraph--1 partner__name">
							{partnerName}
						</p>
						{addressAndPhone}
						<PartnerBadges
							isVar={partner.isVar}
							isVad={partner.isVad}
							isMsp={partner.isMsp}
						/>
					</div>
				);
			}
		});

		return (
			<div>{partnerEntries}</div>
		);
	}
}

if (typeof module !== "undefined") {
	module.exports = FilterablePartnerEntries;
}
